.header__nav{position:fixed;height:25px;left:0;width:100%;top:0;background-color:#f0f0f0;z-index:111}.header__menu{flex-direction:row;align-items:center;justify-content:center;flex-direction:column;position:fixed;display:flex;background-color:rgba(13,13,13,0.95);margin:0;top:25px;padding:0;box-sizing:border-box;opacity:0;transition:0.3s ease all;left:25px}@media screen and (min-width: 768px){.header__menu{flex-direction:row}}.header__menu.active{position:fixed;width:calc(100vw - 65px);height:calc(100vh - 50px);opacity:1;z-index:99}.header__menu-item{list-style-type:none;margin:0 20px}.header__menu-item a{display:inline-block;font-weight:700;font-size:18px;letter-spacing:0.1em;line-height:40px;color:#ffffff;text-decoration:none;text-transform:uppercase;transition:all 100ms linear;opacity:0.70}.header__menu-item a.active,.header__menu-item a:hover{opacity:1;position:relative;display:flex;justify-content:center}.header__menu-item a.active:before,.header__menu-item a:hover:before{content:'';width:80%;height:3px;background-color:#fff;position:absolute;bottom:0}.header__hamburger{width:20px;height:20px;display:block;z-index:99;position:fixed;background:transparent;right:25px;cursor:pointer;top:3px}.header__hamburger span{display:inline-block;position:absolute;left:50%;top:50%;bottom:auto;right:auto;transform:translateX(-50%) translateY(-50%);width:18px;background-color:#000000;height:2px}.header__hamburger span:before,.header__hamburger span:after{content:'';width:100%;transition:0.3s ease all;height:100%;position:absolute;background-color:#000000;right:0}.header__hamburger span:after{transform:translateY(5px) rotate(0deg)}.header__hamburger span:before{transform:translateY(-5px) rotate(0deg)}.header__hamburger.active span{background-color:transparent}.header__hamburger.active span:after{transform:translateY(0) rotate(-45deg)}.header__hamburger.active span:before{transform:translateY(0) rotate(45deg)}

.header__social{position:absolute;bottom:20px;align-items:flex-end;display:flex}.header__social--icon{width:15px;height:15px;background-position:center;background-size:contain;margin:0 10px;opacity:0.75;transition:0.3s ease all}.header__social--icon:hover{opacity:1}.header__social--facebook{background-image:url(/static/media/facebook.1a0a5452.svg)}.header__social--instagram{background-image:url(/static/media/instagram.78e243d0.svg)}.header__social--linkedin{background-image:url(/static/media/linkedin.aedcefbb.svg)}.header__social--vk{background-image:url(/static/media/vk.9aa3a892.svg)}

.Hero_hero__1tGna{height:calc(100vh - 25px);position:relative}.Hero_hero__slide__11Dui{height:100%;display:flex !important;justify-content:center;align-items:center}.Hero_hero__img__1YuK3{width:100%;height:100%;object-position:center;object-fit:cover;max-height:calc(100vh - 50px)}.Hero_hero__title__1LORi{position:absolute;font-size:40px;color:white;margin:0;font-weight:400;max-width:800px}@media screen and (min-width: 1200px){.Hero_hero__title__1LORi{font-size:84px}}.Hero_hero__title__1LORi strong{font-weight:bold}.Hero_hero__1tGna .Hero_slick-dots__1PFEZ{display:flex !important;flex-direction:column;right:-22px;width:25px;top:39%}

.slick-slider .slick-dots{display:flex !important;flex-direction:column;right:-22px;width:25px;top:43%}

*{box-sizing:border-box}label{line-height:24px;margin-bottom:5px;width:100%;color:#0d0d0d;font-size:12px;font-weight:400;text-transform:uppercase;letter-spacing:0.15em}form{padding:20px}input[type=text],input[type=email],select,textarea{font-weight:300;font-size:15px;background:transparent;border:none;border-bottom:1px solid #d9d9d9;line-height:30px;height:45px;color:#0d0d0d;padding:6px 0px;transition:all 200ms linear;border-radius:0px;width:100%;display:block;margin-bottom:40px}input[type=text]:active,input[type=text]:visited,input[type=text]:focus,input[type=email]:active,input[type=email]:visited,input[type=email]:focus,select:active,select:visited,select:focus,textarea:active,textarea:visited,textarea:focus{outline:0}textarea{width:100%;min-height:100px;max-width:100%;line-height:26px}input[type=submit]{cursor:pointer;border-radius:0px;display:inline-block;font-weight:400;font-size:13px;line-height:20px !important;letter-spacing:0.2em;text-transform:uppercase;text-align:center;margin-bottom:5px;transition:all 300ms ease;color:#0d0d0d;border:1px solid rgba(0,0,0,0.2);padding:10px 30px;position:relative;margin-bottom:50px;background-color:transparent}input[type=submit]:hover{background:#0d0d0d;color:#ffffff;border-color:#0d0d0d}

.main-heading{letter-spacing:0.02em;font-weight:700;text-align:center;font-size:28px;margin-bottom:0;color:#0d0d0d;padding:0 20px;max-width:100%}@media screen and (min-width: 1200px){.main-heading{font-size:70px}}.separator{position:relative;width:100%;margin:28px auto 30px auto;height:5px;display:block;display:flex;justify-content:center}.separator span{display:block;position:relative;width:5px;height:5px;background:#0d0d0d;border-radius:10px}.separator span:before,.separator span:after{content:"";vertical-align:top;display:inline-block;width:28px;height:2px;border-bottom:1px solid #0d0d0d;position:absolute;top:0;right:12px}.separator span:after{left:12px;right:inherit}.subHeading{text-align:center;font-weight:300;margin-top:10px;text-transform:uppercase;letter-spacing:0.15em;font-size:18px;line-height:29px;padding:0 20px}@media screen and (min-width: 1200px){.subHeading{font-size:21px}}

.navigation{margin-bottom:50px;-webkit-overflow-scrolling:touch;overflow-x:scroll;overflow-y:hidden;white-space:nowrap;display:flex;padding:0 10px}@media screen and (min-width: 768px){.navigation{display:flex;justify-content:center;align-items:center;padding:0;overflow-x:inherit}}.navigation__item{padding:0 10px 10px 10px}.navigation__item a{display:block;position:relative;font-size:12px;font-weight:400;text-transform:uppercase;letter-spacing:0.2em;color:#0d0d0d;line-height:20px;padding-left:0.1em;text-decoration:none;display:flex;justify-content:center}.navigation__item a.active:after{content:"";position:absolute;bottom:0;width:100%;height:1px;background:#0d0d0d;transition:all 200ms ease}

.works{display:flex;flex-wrap:wrap;background-color:#ffffff;padding:20px;flex-direction:column}@media screen and (min-width: 768px){.works{flex-direction:row}}.works__item{width:calc(100% - 30px);height:250px;position:relative;margin:15px;padding:50px;z-index:99;display:flex;align-items:center;justify-content:center;transition:0.3s ease all;flex-direction:column}@media screen and (min-width: 768px){.works__item{width:calc(50% - 30px);height:450px}}@media screen and (min-width: 1200px){.works__item{width:calc(33% - 30px);height:450px}}.works__item:after{position:absolute;width:100%;height:100%;content:'';background-color:rgba(255,255,255,0)}.works__item:hover h4,.works__item:hover p{display:flex;z-index:11;justify-content:center;width:100%;text-align:center}.works__item:hover h4:after,.works__item:hover p:after{display:block}.works__item:hover:after{position:absolute;width:100%;height:100%;content:'';background-color:rgba(255,255,255,0.96)}.works__item--name{position:relative;display:none;align-items:center;padding-bottom:15px;justify-content:center;margin-bottom:0}.works__item--name:after{content:'';position:absolute;bottom:0;width:40px;height:3px;background-color:#000000;display:none}.works__item--desc{display:none}.works__item img{position:absolute;width:100%;height:100%;top:0;z-index:-1;object-fit:cover}

.about{display:flex;margin-bottom:80px;max-width:100%;flex-direction:column}@media screen and (min-width: 1200px){.about{flex-direction:row}}.about h4{font-size:21px;line-height:29px;font-weight:400;text-transform:uppercase;color:#0d0d0d;letter-spacing:0.02em;position:relative;padding-bottom:10px}.about h4:after{width:40px;height:1px;line-height:1px;background:#0d0d0d;display:block;content:'';position:absolute;bottom:0}.about__item{padding:25px}

.video-wrapper{width:100%;height:100%;display:block;background:url(/static/media/avocado-video.215cf97e.mp4);background-size:cover;background-position:center}.video-wrapper .video{top:0;left:0;overflow:hidden;width:100%;height:300px}@media screen and (min-width: 1200px){.video-wrapper .video{width:50%;position:absolute;height:100%}}.video-wrapper .video video{height:100%}

.video-with-text{display:flex;margin-bottom:80px;position:relative;flex-direction:column}@media screen and (min-width: 1200px){.video-with-text{flex-direction:row}}@media screen and (min-width: 1200px){.video-with-text>div{width:50%}}.video-with-text__green-column{background-color:#66cc33}.video-with-text__green-column--content{padding:60px 20px;max-width:500px;margin:0 auto;color:#fff}@media screen and (min-width: 1200px){.video-with-text__green-column--content{padding:120px 20px}}.video-with-text__green-column--content h5{text-transform:uppercase;letter-spacing:0.15em;font-size:22px;font-weight:400;color:#fff}.video-with-text__green-column--content p{color:#fff;margin-top:20px}

.services{display:flex;flex-wrap:wrap;align-items:flex-start;justify-content:center;margin-top:100px;flex-direction:column;max-width:100%}@media screen and (min-width: 1200px){.services{flex-direction:row}}.services__item{margin-bottom:60px;padding:0 15px;box-sizing:border-box}@media screen and (min-width: 1200px){.services__item{width:33%}}.services__item--icon{width:60px;display:block;margin-bottom:25px;margin:auto}.services__item--title{text-align:center;text-transform:uppercase;font-size:22px;position:relative;display:flex;justify-content:center;padding-bottom:15px}.services__item--title:after{content:'';width:40px;height:1px;line-height:1px;background:#0d0d0d;display:block;bottom:0;position:absolute}

.testimonials{background:linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)),url(/static/media/testimonials-background2.4d10dd89.jpg);padding:20px;background-position:center;background-size:cover}.testimonials .testimonial{color:#fff;display:flex !important;flex-direction:column;align-items:center;min-height:500px;justify-content:center}.testimonials .testimonial__name{font-weight:700;letter-spacing:0.1em;color:#ffffff;text-transform:uppercase}.testimonials .testimonial__company{font-size:13px;line-height:16px;margin-top:5px;color:#ffffff;font-weight:300}.testimonials .testimonial p{letter-spacing:0.02em;color:#ffffff;max-width:600px;text-align:center;line-height:1.6;font-size:26px}

